
.Application{
  direction: flex;
  align-items: center;
}

.rowDiv{

  display: flex;

  text-align: center;
}

.rowDivdel{
  display:flex;
  flex-direction: row;
  flex: 1;
}
.rowDivsub{
  margin:auto;
  margin-top: 3vh;
  margin-left: 60vw;
}
.inputPart{
text-align: center;
flex:1;
align-items: center;
}

.inputPartdel{
  text-align: right;
  flex:1;
  align-items: center;
  max-width: 30vw ;
  }
.tableWrapperdel{
  width: 80vw;
}
.valuePart{

flex :1;
text-align: center;
align-items: center;
}

.tableData{
  width: 80vw;
  text-align: center;
}

table {
  border-collapse: collapse;
  margin:auto;
  width: 80vw;
}

table, tr, th, td{
  border: 1px solid black;
}

.tableWrapper{
  overflow-x: auto;
   margin-top: 5vh;
}

.tableWrapperdel{
  display: flex;
  flex-direction: column;
  overflow-x: auto;
   margin-top: 5vh;
   margin: auto;
}

.addDiv{
  float: right;
}
.mainDiv{
  display:flex;
  flex-direction: column;
}
.addButton{
  height: 3vh;
  float:right;
  margin-right: 10vw;
  margin-top: 10vh;
  }

#submitButton{
  border-width: 1px;
  border-style: solid;
  border-color: grey;
  border-radius: 6%;
  padding: 2%;
}